import React, {Component} from 'react';
import { withTranslation } from 'react-i18next';
import { Col, Row, Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import Icon from 'react-eva-icons';

import {filtres} from '../../../containers/Dispositif/data';
import {randomColor} from '../../Functions/ColorFunctions';

import './ContributeurModal.scss'

class ContributeurModal extends Component {
  state={
    structure:'',
  }

  render() {
    const {t, show, toggleModal, name} = this.props;
    return (
      <Modal isOpen={show} toggle={()=> toggleModal(false, name)} className='modal-contributeur'>
        <ModalHeader>
          C'est parti !
        </ModalHeader>
        <ModalBody>
          <h3>Quels sont vos thèmes de prédilection ?</h3>
          <Row className="align-items-center themes">
            {filtres.tags.map(tag =>(
              <Col col="6" sm="4" md="2" xl className="mb-3 mb-xl-0" key={tag}>
                <Button block outline color={randomColor()} onClick={()=>this.queryDispositifs({'tags':tag})}>
                  {t("Tags." + tag)}
                </Button>
              </Col>
            ))}
          </Row>
          <h3>Quelle structure représentez-vous ?</h3>
        </ModalBody>
        <ModalFooter>

        </ModalFooter>
      </Modal>
    )
  }
}

export default withTranslation()(ContributeurModal);